export default class GlobalVars {
    static domain = '';
    static userId = 'leht';
    static username = 'leht';
    
    static formId = '';

    static lstOTsData = [];
    static lstCommonInfosData = [];

    static hasOTs = true;

    static showElementPanel() {
        $('#contentElement').css({display: 'block'});
        $('#contentContainer').css({display: 'none'});
    
        $('#tabElement').addClass('tab-active');
        $('#tabContainer').removeClass('tab-active');
    }

    static showContainerPanel() {
        $('#contentElement').css({display: 'none'});
        $('#contentContainer').css({display: 'block'});
    
        $('#tabElement').removeClass('tab-active');
        $('#tabContainer').addClass('tab-active');
    }
}