import FormElement from "./form-element";
import Constants from "../constants";

export default class FormCommonInfo extends FormElement {
    constructor(elementInfo, parent, idx, parentPanel) {
        super(elementInfo, parent, idx, parentPanel);
	
        this._type = Constants.ELEMENT_TYPE.COMMON_INFO;

        this._domLabel = null;
        this._domSpan = null;

        this._commonInfo = {};
        this._recordValue = '';

        //specific information
        if(elementInfo != null) {
            if(elementInfo.title != undefined)
                this._commonInfo.title = elementInfo.title;
            if(elementInfo.defaultField != undefined)
                this._commonInfo.defaultField = elementInfo.defaultField;
            if(elementInfo.name != undefined)
                this._commonInfo.name = elementInfo.name;
        }
    }

    dispose() {
        if(this._domSpan != null)
            this._domSpan.remove();

        if(this._domLabel != null)
            this._domLabel.remove();

        super.dispose();
    }

    _createContent(containerDiv, mainDomElement) {
        //create span label
        let domSpan = $('<span>').addClass('form-common-info');

        //apply styles
        this._setDomStyles(domSpan);

        //set value
        domSpan.html('[' + this._commonInfo.defaultField + ']');

        //add to main DomElement
        mainDomElement.append(domSpan);

        return domSpan;
    }

    _createLabel(containerDiv, mainDomElement) {
        //create span label
        this._domLabel = $('<div>').addClass('form-input-label');

        this._setLabelContent();

        // //show full label content (OT name) when hover on the label
        // this._domLabel.on('mouseenter', function(ev) {

        // });

        //add to main DomElement
        mainDomElement.append(this._domLabel);
    }

    _setLabelContent() {
        //show part of the OT name
        let span = $('<span>');
        this._domLabel.append(span);
        if(this._commonInfo.name == undefined)
            return;

        span.attr('title', this._commonInfo.name);
        if(this._commonInfo.name.length > 30) {
            let s = this._commonInfo.name.substr(0, 28) + '..';
            span.html(s);
        }
        else
            span.html(this._commonInfo.name);
    }

    render4Editing(containerDiv) {
        // console.log('label render4Editing');
        //create main DomElement
        this._domElement = this._createDomElement(containerDiv, true);

        //my specific content
        this._domSpan = this._createContent(containerDiv, this._domElement);
        this._createLabel(containerDiv, this._domElement);
    }

    render4Input(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        let domSpan = this._createContent(containerDiv, domElem);

        //set value
        domSpan.html(this._recordValue);
    }

    render4Printing(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        let domSpan = this._createContent(containerDiv, domElem);

        //set value
        domSpan.html(this._recordValue);

        //update height bounding base on content
        let height = domSpan.height();
        if(height > this._bounding.height) {
            this._bounding.height = height;
            domElem.css({ height: height + 'px' });
            this._parentPanel.updateElementBounds(this._bounding, this._idx);
        }
    }

    onUnSelect() {
        // console.log('label onUnSelect');
        super.onUnSelect();
    }

    _setDomStyles(domSpan) {
        //undefined in case of editing
        if(domSpan == undefined)
            domSpan = this._domSpan;

        domSpan.css(this._styles);
    }
    
    _setDomStyleSingle(prop, val, domSpan) {
        if(domSpan == undefined)
            domSpan = this._domSpan;

        let obj = {};
        obj[prop] = val;
        domSpan.css(obj);
    }
    
    genElementInfo() {
        let elementInfo = super.genElementInfo();
        // elementInfo.commonInfo = {};
        // Object.assign(elementInfo.commonInfo, this._commonInfo);

        elementInfo.title = this._commonInfo.title;
        elementInfo.defaultField = this._commonInfo.defaultField;
        elementInfo.name = this._commonInfo.name;

        return elementInfo;
    }

    updateCommonInfo(dataObj) {
        console.log('updateCommonInfo', dataObj);
        Object.assign(this._commonInfo, dataObj);

        this._domLabel.html('');
        this._setLabelContent();

        this._domSpan.html('[' + this._commonInfo.defaultField + ']');
    }

    setCommonInfoValue(value) {
        this._recordValue = value;
    }
}