/**
 * Panel object for each panel in the form.
 *
 * @module containers/Panel
 */

import Constants from "../constants";

export default class Panel {
    constructor(parentForm, parentPage, idx, layoutType, positionType, panelInfo = null) {
        this._parentForm = parentForm;
        this._parentPage = parentPage;
        this._idx = idx;

        this._layoutType = layoutType;
        this._positionType = positionType;

        if(panelInfo != null) {
            this._bounding = {
                left: panelInfo.left,
                top: panelInfo.top,
                width: panelInfo.width,
                height: panelInfo.height,
                right: panelInfo.right,
                bottom: panelInfo.bottom
            };
            this._hasVisibleCondition = panelInfo.hasVisibleCondition;
            this._conditionFieldName = panelInfo.conditionFieldName;
            this._paddingBottom = (panelInfo.paddingBottom != undefined) ? panelInfo.paddingBottom : Constants.DEFAULT_PANEL_PADDING_BOTTOM;
        }
        else { 
            //default params
            this._bounding = {
                left: 0,
                top: 0,
                width: '100%',
                height: Constants.DEFAULT_PANEL_HEIGHT
            };
            this._hasVisibleCondition = false;
            this._conditionFieldName = null;
            this._paddingBottom = Constants.DEFAULT_PANEL_PADDING_BOTTOM;
        }

        this._maxElementHeight = 0;
        this._maxElementIdx = -1;

        this._offsetLeft = 0;
        this._offsetTop = 0;

        this._selectionCtrl = null;
        this._startSelectPos = null;
        this._curSelection = null;

        this._isVisibleByCondition = true;

        //styles
        this._styles = {};

        //jQuery main HTML elements
        this._domElement = null; //main
        this._ctrlElement = null; //controlling (select)
    }

    get idx() {
        return this._idx;
    }

    set idx(val) {
        this._idx = val;
    }

    genElementInfo() {
        let elementInfo = {
            idx: this._idx,
            pageIdx: this._parentPage._order,
            layoutType: this._layoutType,
            positionType: this._positionType,
            hasVisibleCondition: this._hasVisibleCondition,
            conditionFieldName: this._conditionFieldName,
            left: this._bounding.left,
            top: this._bounding.top,
            width: this._bounding.width,
            height: this._bounding.height,
            paddingBottom: this._paddingBottom
        };
        return elementInfo;
    }

    render4Editing(containerDiv) {
        //create main DomElement
        this._domElement = this._createDomElement(containerDiv, true);

        this._selectionCtrl = $('<div>').addClass('multi-selecting');
        this._domElement.append(this._selectionCtrl);

        // //apply styles
        // this._setDomStyles(this._domElement);
    }

    render4Input(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        return domElem;
    }

    render4Printing(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        if(this._isVisibleByCondition == false) {
            domElem.css({ display: 'none' });
        }

        return domElem;
    }

    postRendering(domElem) {
        this.refreshHeight(domElem, false);
    }

    refreshHeight(domElem, forceBoundingHeight) {
        if(this._positionType == Constants.PANEL_POSITION_TYPE.RELATIVE && this._maxElementHeight + this._paddingBottom > this._bounding.height) {
            domElem.css({
                height: (this._maxElementHeight + this._paddingBottom) + 'px'
            });
        }
        else if(forceBoundingHeight){
            domElem.css({
                height: this._bounding.height + 'px'
            });
        }
    }

    _createDomElement(containerDiv, isEditing) {
        let domElement = $('<div>');
        if(isEditing) {
            domElement.addClass('form-panel').attr("dragable", true);
        }

        //TODO: apply position & size
        if(this._positionType == Constants.PANEL_POSITION_TYPE.ABSOLUTE) {
            domElement.css({
                position: 'absolute',
                left: this._bounding.left,
                top: this._bounding.top,
                width: this._bounding.width,
                height: this._bounding.height + 'px',
            });
        }
        else {
            domElement.css({
                position: 'relative',
                width: this._bounding.width,
                height: this._bounding.height + 'px',
            });
        }

        //TODO: apply common styles if needed

        //add to containerDiv
        containerDiv.append(domElement);

        //event handling
        if(isEditing) {
            var _self = this;
            domElement.on('mousedown', function(ev) {
                if(!_self._parentForm._moving2PanelMode)
                    _self.onMouseDown(ev);
                ev.stopPropagation();
            })
            .on('click', function(ev) {
                if(!_self.isSelected()) {
                    console.log('panel click here');
                    //unselect other panels if ctrl, shift button not hold
                    if(ev.shiftKey != true && ev.ctrlKey != true)
                        _self._parentForm.unSelectAllPanel();
                    
                    _self._parentForm.selectPanel(_self);
                    // _self.onSelect(ev);

                    if(_self._parentForm._moving2PanelMode) {
                        _self._parentForm.dropSelectingElems2Panel(_self);
                    }
                }
                ev.stopPropagation();
            });

            //drag/drop elements to panel
            domElement.on('dragenter', function(e){
                console.log('panel dragenter');
                this.classList.add('panel-dragover');
            });
            domElement.on('dragover', function(e){
                console.log('panel dragover');
                if (e.originalEvent.preventDefault) {
                    e.originalEvent.preventDefault(); // Necessary. Allows us to drop.
                }
            
                e.originalEvent.dataTransfer.dropEffect = 'move';  // See the section on the DataTransfer object.
            
                this.classList.add('panel-dragover');
            
                return false;
            });
            domElement.on('dragleave', function(e){
                this.classList.remove('panel-dragover');
            });
            domElement.on('drop',  function(e){
                _self.handleDrop()
            });
        }
        

        return domElement;
    }

    select() {
        this._domElement.addClass('panel-active');
    }

    unSelect() {
        this._domElement.removeClass('panel-active');
    }

    isSelected() {
        return (this._parentForm._selectedPanelIdx == this._idx);
    }

    handleDrop() {
        console.log('panel handleDrop');
        //call parent's changing method (change selecting elements from 1 panel to this panel, set new positions)
        this._parentForm.dropSelectingElems2Panel(this);
    }

    updateElementBounds(bounding, idx) {
        let bottom = bounding.top + bounding.height;
        if(this._maxElementHeight < bottom) {
            this._maxElementHeight = bottom;
            this._maxElementIdx = idx;
        }
    }

    onElementsMoved(bounding, idx) {
        let bottom = bounding.top + bounding.height;
        if(this._maxElementHeight < bottom) {
            this._maxElementHeight = bottom;
            this._maxElementIdx = idx;
            this.postRendering(this._domElement);
        }
        else if(this._maxElementHeight > bottom &&  this._maxElementIdx == idx) {
            //re-calculate max height
            this.recalcElementBounds();
        }
    }

    recalcElementBounds() {
        this._maxElementHeight = 0;
        let len = this._parentForm._lstElements.length;
        for(let j = 0; j < len; j++) {
            let elem = this._parentForm._lstElements[j];
            if(elem._parentPanel.idx == this._idx)
                this.updateElementBounds(elem._bounding, elem._idx);
        }
        this.postRendering(this._domElement);
    }

    getDomOffset() {
        let offset = this._domElement.offset();
        this._offsetLeft = offset.left;
        this._offsetTop = offset.top;
        // console.log('panel ' + this._idx + ' getDomOffset:', offset);
    }

    onMouseDown(ev) {
        // console.log(ev);
        var _self = this;

        //get correct relative position
        let curOffset = this._domElement.offset();
        let x = ev.pageX - curOffset.left;
        let y = ev.pageY - curOffset.top;

        //show the selecting div
        this._selectionCtrl.css({
            display: 'block',
            top: x + 'px',
            left: y + 'px',
            width: '2px',
            height: '2px'
        });
        this._startSelectPos = {
            x: x,
            y: y
        };
        this._curSelection = {
            x: x,
            y: y,
            w: 2,
            h: 2
        };

        this._domElement.on('mousemove', function(ev) {
            console.log('panel mousemove');
            _self.onMouseMove(ev);
            ev.preventDefault();
        })
        .on('mouseup', function(ev) {
            console.log('panel mouseup');
            _self.onMouseUp(ev);
            ev.preventDefault();
        })
        .on('mouseleave', function(ev) {
            console.log('panel mouseleave');
            _self.onMouseUp(ev);
            ev.preventDefault();
        });
    }
    onMouseMove(ev) {
        //get correct relative position
        let curOffset = this._domElement.offset();
        let x = ev.pageX - curOffset.left;
        let y = ev.pageY - curOffset.top;

        if(x < this._startSelectPos.x) {
            this._curSelection.x = x;
            this._curSelection.w = this._startSelectPos.x - this._curSelection.x;
        }
        else {
            this._curSelection.x = this._startSelectPos.x;
            this._curSelection.w = x - this._curSelection.x;
        }

        if(y < this._startSelectPos.y) {
            this._curSelection.y = y;
            this._curSelection.h = this._startSelectPos.y - this._curSelection.y;
        }
        else {
            this._curSelection.y = this._startSelectPos.y;
            this._curSelection.h = y - this._curSelection.y;
        }

        this._selectionCtrl.css({
            left: this._curSelection.x + 'px',
            top: this._curSelection.y + 'px',
            width: this._curSelection.w + 'px',
            height: this._curSelection.h + 'px'
        });
        
    }
    onMouseUp(ev) {
        this._domElement.off('mousemove').off('mouseup').off('mouseleave');
        // console.log('_curSelection: (' + this._curSelection.x + ', ' + this._curSelection.y + ', ' + this._curSelection.w + ', ' + this._curSelection.h + ')');

        //select elements
        this._parentForm.unSelectAll();

        this.getDomOffset();

        let len = this._parentForm._lstElements.length;
        for(let i = 0; i < len; i++) {
            let elem = this._parentForm._lstElements[i];
            if(elem._parentPanel.idx != this.idx)
                continue;
                
            let bound = elem._bounding;
            // let left = bound.left + this._offsetLeft;
            // let top = bound.top + this._offsetTop;

            // console.log('bounding: (' + bound.left + ', ' + bound.top + ', ' + bound.width + ', ' + bound.height + ')');
            if(bound.left >= this._curSelection.x && bound.top >= this._curSelection.y
                && bound.left + bound.width <= this._curSelection.x + this._curSelection.w
                && bound.top + bound.height <= this._curSelection.y + this._curSelection.h) {
                    this._parentForm.selectElement(elem);
                    elem.onSelect(null);
                }
            
        }

        //event updating UI again if no control selected
        if(this._parentForm._lstSelectedElems.length == 0)
            this._parentForm.updateUIElementData();

        //hide the selecting div
        this._selectionCtrl.css({
            display: 'none'
        });
    }
}