import ResizeAnchor from "./resize-anchor";

/**
 * FormElement object for each element in the form.
 *
 * @module elements/FormElement
 */

 export default class FormElement {
    constructor(elementInfo, parent, idx, parentPanel) {
        this._parentForm = parent;
        this._parentPanel = parentPanel;
        // this._parentPanelIdx = elementInfo.panelIdx;
        // if(this._parentPanelIdx == undefined || this._parentPanelIdx >= this._parentForm._lstPanels.length)
        //     this._parentPanelIdx = 0;

        //common info
        this._idx = idx;
        this._type = 'GENERAL';

        //styles
        this._styles = {};

        //jQuery main HTML elements
        this._domElement = null; //main
        this._ctrlElement = null; //controlling (select, resize, move)
        this._arrAnchors = []; //anchors for controlling

        if(elementInfo != null) {
            this._bounding = {
                left: elementInfo.left,
                top: elementInfo.top,
                width: elementInfo.width,
                height: elementInfo.height
            };

            if(elementInfo.formatStyle != undefined) {
                Object.assign(this._styles, elementInfo.formatStyle);
                this._styleBackwardCompatable();
            }

            this._value = elementInfo.value;
        }
        else { 
            //default params
            this._bounding = {
                left: 10,
                top: 10,
                width: 80,
                height: 32
            };
            this._value = null;
        }

        //for controlling
        this._isSelected = false;
        this._dragStart = {
            x: -100,
            y: -100
        };

        this._cloningByDragStep = 0;
    }

    getType() {
        return this._type;
    }

    dispose() {
        this._arrAnchors.forEach((item) => {
            item.dispose();
        });
        this._arrAnchors = [];

        if(this._ctrlElement != null)
            this._ctrlElement.remove();

        if(this._domElement != null)
            this._domElement.remove();

        this._styles = undefined;
        this._bounding = undefined;
    }

    //
    // return domElement for using in 3 cases: editing, inputing, printing
    // editing case: ctrlElement is assigned directly to member object, not returned
    //
    _createDomElement(containerDiv, isEditing) {
        let domElement = $('<div>');

        //TODO: apply position & size
        domElement.css({
            position: 'absolute',
            left: this._bounding.left,
            top: this._bounding.top,
            width: this._bounding.width,
            height: this._bounding.height,
        });

        //TODO: apply common styles if needed

        //add to containerDiv
        containerDiv.append(domElement);

        this._parentPanel.updateElementBounds(this._bounding, this._idx);

        //controlling 
        if(isEditing) {
            this._ctrlElement = $('<div>').addClass('controll-div').attr('tabindex', '-1'); //tabindex=-1 for receiving keydown event

            //TODO: apply position & size
            this._ctrlElement.css({
                position: 'absolute',
                left: this._bounding.left,
                top: this._bounding.top,
                width: this._bounding.width,
                height: this._bounding.height
            });
    
            //event handling
            this._registerEventsHandling();
    
            //add to containerDiv
            containerDiv.append(this._ctrlElement);

            //add anchors
            this._updateAnchors(true, -1, containerDiv);           
        }

        return domElement;
    }

    _registerEventsHandling() {
        var _self = this;
        this._ctrlElement.on('mousedown', function(ev) {
            ev.stopPropagation();
        })
        .on('click', function(ev) {
            if(_self._cloningByDragStep < 0) {
                console.log('click here 0');
                _self._cloningByDragStep = 0;
            }
            else if(!_self._isSelected) {
                console.log('click here');
                //unselect other elements if ctrl, shift button not hold
                if(ev.shiftKey != true && ev.ctrlKey != true)
                    _self._parentForm.unSelectAll();
                
                _self._parentForm.selectElement(_self);
                _self.onSelect(ev);
            }
            ev.stopPropagation();
        })
        .on('keydown', function(ev) {
            console.log('keydown on element ctrl item');
            if(_self._parentForm.processKeyDown(ev)) {
                ev.stopPropagation();
                ev.preventDefault();
            }
        })
        .contextmenu(function(ev) {
            console.log('contextmenu here');
            ev.stopPropagation();
            ev.preventDefault();
            //select if not selecting
            if(!_self._isSelected) {
                _self._parentForm.unSelectAll();
                _self._parentForm.selectElement(_self);
                _self.onSelect(ev);
            }
            //show context menu
            $('#divElemContextMenu').css({
                left: ev.pageX + 'px',
                top: ev.pageY + 'px'
            })
            .off('mouseleave')
            .on('mouseleave', function(){
                $('#divElemContextMenu').hide();
            })
            .show();
        });
        this._ctrlElement.draggable({
            drag: function(event, ui) {
                // console.log(ui);
                event.stopPropagation();

                if(!_self._isSelected)
                    return;
                
                if(Math.abs(ui.position.left - _self._dragStart.x) + Math.abs(ui.position.top - _self._dragStart.y) < 2)
                    return;
                
                //move if ctrl button not hold
                //else: clone selecting elems
                if(!event.ctrlKey)
                    _self.onMove(ui.position);
                else {
                    console.log('here, ' + _self._cloningByDragStep);
                    if(_self._cloningByDragStep <= 0) {
                        _self._cloningByDragStep = 1;
                        _self.onClone(ui.position);
                    }
                    else {
                        _self.onCloneMove(ui.position);
                    }
                }
                    
            },
            start: function(event, ui) {
                event.stopPropagation();

                if(!_self._isSelected)
                    return;
                
                // mouse position
                _self._dragStart.x = ui.position.left;
                _self._dragStart.y = ui.position.top;
            },
            stop: function(event, ui) {
                event.stopPropagation();
                _self.onDragStop(ui.position);
                // console.log('onDragStop here');
                if(_self._cloningByDragStep > 0) {
                    _self._cloningByDragStep = -1; //will set to 0 in click event
                    _self.onUnSelect();
                    _self._parentForm.showClonedElemsSelected();
                }
            }
        });
    }

    _updateAnchors(isCreate, exceptAnchor = -1, containerDiv = null) {
        // let containerDiv = this._parentForm._container;
        //add/update anchors, anchorType:
        // 7   0   1
        // 6       2
        // 5   4   3
        let halfW = this._bounding.width / 2;
        let halfH = this._bounding.height / 2;
        let pos = {
            x: this._bounding.left,
            y: this._bounding.top
        };
        let arrPosAddition = [
            { x: halfW, y: 0 },
            { x: halfW, y: 0 },
            { x: 0, y: halfH },
            { x: 0, y: halfH },
            { x: -halfW, y: 0 },
            { x: -halfW, y: 0 },
            { x: 0, y: -halfH },
            { x: 0, y: -halfH },
        ];
        for(let i = 0; i < 8; i++) {
            pos.x += arrPosAddition[i].x;
            pos.y += arrPosAddition[i].y;
            if(isCreate) {
                let anchor = new ResizeAnchor(this, i, pos, containerDiv);
                this._arrAnchors.push(anchor);
            }
            else if(i != exceptAnchor) {
                this._arrAnchors[i].updatePosition(pos);
            }
            
        }
    }

    render4Editing(containerDiv) {
        console.log('render4Editing need implementation!');
    }

    render4Input(containerDiv) {
        console.log('render4Input need implementation!');
    }

    render4Printing(containerDiv) {
        console.log('render4Printing need implementation!');
    }

    onSelect(ev) {
        this._isSelected = true;
        this._ctrlElement.addClass('selecting').removeClass('cloning');
        for(let i = 0; i < 8; i++) {
            this._arrAnchors[i].show();
        }
    }

    onUnSelect() {
        if(this._isSelected) {
            this._isSelected = false;
            this._ctrlElement.removeClass('selecting');
            for(let i = 0; i < 8; i++) {
                this._arrAnchors[i].hide();
            }
        }
    }

    onMove(position) {
        let toLeft = false, toBottom = false;
        if(position.left > this._dragStart.x)
            toLeft = true;
        if(position.top > this._dragStart.y)
            toBottom = true;

        //update bounding
        let oldX = this._bounding.left;
        let oldY = this._bounding.top;
        this._bounding.left += position.left - this._dragStart.x;
        this._bounding.top += position.top - this._dragStart.y;

        this._dragStart.x = position.left;
        this._dragStart.y = position.top;

        //check & show align if suitable
        let alignedDelta = this._parentForm.checkAlignment(this, toLeft, toBottom, true);
        let needUpdate = false;
        let eps = 3;
        if(alignedDelta.x > -100 && Math.abs(alignedDelta.x) < eps) {
            this._bounding.left += alignedDelta.x;
            needUpdate = true;
        }
        if(alignedDelta.y > -100 && Math.abs(alignedDelta.y) < eps) {
            this._bounding.top += alignedDelta.y;
            needUpdate = true;
        }

        //update dom
        this._domElement.css({
            left: this._bounding.left,
            top: this._bounding.top
        });

        this._updateAnchors(false);

        this._parentPanel.onElementsMoved(this._bounding, this._idx);

        //NOT WORK FOR NOW!!!
        //update ctrl element if we have correction
        if(needUpdate) {
            this._ctrlElement.css({
                left: this._bounding.left,
                top: this._bounding.top
            });
        }

        if(this._parentForm._lstSelectedElems.length > 1) {
            let delta = {
                x: this._bounding.left - oldX,
                y: this._bounding.top - oldY
            };
            this._parentForm.moveSelectingElements(delta, this);
        }
    }

    onMoveInactive(delta) {
        //update bounding
        this._bounding.left += delta.x;
        this._bounding.top += delta.y;

        //update dom
        this._domElement.css({
            left: this._bounding.left,
            top: this._bounding.top
        });

        this._ctrlElement.css({
            left: this._bounding.left,
            top: this._bounding.top
        });

        this._updateAnchors(false);
        this._parentPanel.onElementsMoved(this._bounding, this._idx);
    }

    onDragStop() {
        // console.log('onDragStop');
        //update _ctrlElement to the correct bounding position
        this._ctrlElement.css({
            left: this._bounding.left,
            top: this._bounding.top
        });

        this._updateAnchors(false);

        this._parentForm.hideAlignment();
    }

    onClone(position) {
        let delta = {
            x: position.left - this._dragStart.x,
            y: position.top - this._dragStart.y
        };

        this._dragStart.x = position.left;
        this._dragStart.y = position.top;

        this._parentForm.cloneSelectingElements(delta, this);
    }

    onCloneMove(position) {
        let delta = {
            x: position.left - this._dragStart.x,
            y: position.top - this._dragStart.y
        };

        this._dragStart.x = position.left;
        this._dragStart.y = position.top;

        this._parentForm.moveSelectingElements(delta, this);
    }

    showCloneState() {
        this._ctrlElement.addClass('cloning');
    }

    genElementInfo() {
        let elementInfo = {
            type: this._type,
            panelIdx: this._parentPanel.idx,
            left: this._bounding.left,
            top: this._bounding.top,
            width: this._bounding.width,
            height: this._bounding.height,
            formatStyle: {}
        };
        Object.assign(elementInfo.formatStyle, this._styles);

        if(this._value != null)
            elementInfo.value = this._value;

        return elementInfo;
    }

    onResize() {

    }

    // anchorType: one of the following
    //      7   0   1
    //      6       2
    //      5   4   3
    onAnchorDrag(srcAnchor) {
        // console.log('onAnchorDrag, srcAnchor: (' + srcAnchor._position.x + ', ' + srcAnchor._position.y + '), type: ' + srcAnchor._type);
        // console.log('bounding: (' + this._bounding.left + ', ' + this._bounding.top + ', ' + this._bounding.width + ', ' + this._bounding.height + ')');
        //check valid & update bounding
        let type = srcAnchor._type;
        if(type <= 1 || type >= 7) {
            if(srcAnchor._position.y >= this._bounding.top + this._bounding.height - 8)
                return false;
            this._bounding.height -= srcAnchor._position.y - this._bounding.top;
            this._bounding.top = srcAnchor._position.y;
        }

        if(type >= 5) {
            if(srcAnchor._position.x >= this._bounding.left + this._bounding.width - 8)
                return false;
            this._bounding.width -= srcAnchor._position.x - this._bounding.left;
            this._bounding.left = srcAnchor._position.x;
        }
        
        if(type >= 1 && type <= 3) {
            if(srcAnchor._position.x <= this._bounding.left + 8)
                return false;
            this._bounding.width = srcAnchor._position.x - this._bounding.left;
        }
        
        if(type >= 3 && type <= 5) {
            if(srcAnchor._position.y <= this._bounding.top + 8)
                return false;
            this._bounding.height = srcAnchor._position.y - this._bounding.top;
        }

        //update other anchors' position, except srcAnchor (is dragging)
        // console.log('here before update anchors');
        // console.log('bounding: (' + this._bounding.left + ', ' + this._bounding.top + ', ' + this._bounding.width + ', ' + this._bounding.height + ')');
        this._updateAnchors(false, srcAnchor._type);

        //update dom & ctrl elements
        this._domElement.css({
            left: this._bounding.left,
            top: this._bounding.top,
            width: this._bounding.width + 'px',
            height: this._bounding.height + 'px'
        });
        this._ctrlElement.css({
            left: this._bounding.left,
            top: this._bounding.top,
            width: this._bounding.width + 'px',
            height: this._bounding.height + 'px'
        });
        return true;
    }

    onAnchorDrop() {
        //update bounding
    }

    clone(newIdx) {

    }

    _styleBackwardCompatable() {
        if(this._styles.align != undefined) {
            let align = this._styles.align;
            if(align === "textalign-left")
                this._styles["text-align"] = "left";
            else if(align === "textalign-right")
                this._styles["text-align"] = "right";
            else if(align === "textalign-center")
                this._styles["text-align"] = "center";

            this._styles.align = undefined;
        }

        if(this._styles.bgColor != undefined) {
            let val = this._styles.bgColor;
            this._styles["background-color"] = val;
            this._styles.bgColor = undefined;
        }

        if(this._styles.font != undefined) {
            let val = this._styles.font;
            this._styles["font-family"] = val;
            this._styles.font = undefined;
        }

        if(this._styles.fontSize != undefined) {
            let val = this._styles.fontSize;
            this._styles["font-size"] = val + 'px';
            this._styles.fontSize = undefined;
        }

        if(this._styles.isBold != undefined && this._styles.isBold == true) {
            this._styles["font-weight"] = 'bold';
            this._styles.isBold = undefined;
        }

        if(this._styles.isItalic != undefined && this._styles.isItalic == true) {
            this._styles["font-style"] = 'italic';
            this._styles.isItalic = undefined;
        }

        if(this._styles.isUnderline != undefined && this._styles.isUnderline == true) {
            this._styles["text-decoration"] = 'underline';
            this._styles.isUnderline = undefined;
        }
    }

    getStyle(prop) {
        return this._styles[prop];
    }

    setStyle(prop, val) {
        this._styles[prop] = val;
        this._setDomStyleSingle(prop, val);
    }

    removeStyle(prop) {
        this._styles[prop] = undefined;
        this._setDomStyles();
    }

    _setDomStyles() {
        console.log('_setDomStyles need implement! Type: ' + this._type);
    }

    _setDomStyleSingle(prop, val) {
        console.log('_setDomStyleSingle need implement! Type: ' + this._type);
    }

    updateObservationType(dataObj) {
        console.log('DEV - Need implementation updateObservationType!');
    }

    updateCommonInfo(dataObj) {
        console.log('DEV - Need implementation updateObservationType!');
    }

    setParentPanel(parentPanel) {
        console.log('setParentPanel');
        
        this._parentPanel = parentPanel;

        //move dom element to new parent panel
        this._domElement.remove();
        this._ctrlElement.remove();
        parentPanel._domElement.append(this._domElement);
        parentPanel._domElement.append(this._ctrlElement);
        this._registerEventsHandling();

        for(let i = 0; i < 8; i++) {
            this._arrAnchors[i]._dom.remove();
            parentPanel._domElement.append(this._arrAnchors[i]._dom);
            this._arrAnchors[i]._registerEventsHandling();
        }
    }
}