import FormElement from "./form-element";
import Constants from "../constants";

export default class FormImage extends FormElement {
    
    constructor(elementInfo, parent, idx, parentPanel) {
        super(elementInfo, parent, idx, parentPanel);
	
        this._type = Constants.ELEMENT_TYPE.IMAGE;

        this._domSpan = null;

        this._imageSource = null;
        this._imageSizeOpt = Constants.IMAGE_SIZE_OPTION.FIT;

        //specific information
        if(elementInfo != null) {
            if(elementInfo.imageSrc != undefined)
                this._imageSource = elementInfo.imageSrc;

            if(elementInfo.imageSizeOption != undefined)
                this._imageSizeOpt = elementInfo.imageSizeOption;
        }
    }

    dispose() {
        if(this._domSpan != null)
            this._domSpan.remove();

        super.dispose();
    }

    _createContent(containerDiv, mainDomElement) {
        //create span label
        let domSpan = $('<img>').addClass('form-image');

        //apply styles
        this._setDomStyles(domSpan, mainDomElement);

        if(this._imageSizeOpt == Constants.IMAGE_SIZE_OPTION.STRETCH)
            domSpan.addClass('form-image-stretch');
        else if(this._imageSizeOpt == Constants.IMAGE_SIZE_OPTION.FIT)
            domSpan.addClass('form-image-fit');
        else if(this._imageSizeOpt == Constants.IMAGE_SIZE_OPTION.CENTER)
            domSpan.addClass('form-image-center');

        //set value
        if(this._imageSource != null)
            domSpan.attr('src', this._imageSource);
        else
            domSpan.attr('src', Constants.DEFAULT_IMAGE_PLACEHOLDER_SRC);

        //add to main DomElement
        mainDomElement.append(domSpan);

        return domSpan;
    }

    render4Editing(containerDiv) {
        // console.log('label render4Editing');
        //create main DomElement
        this._domElement = this._createDomElement(containerDiv, true);

        //my specific content
        this._domElement.css({ overflow: 'hidden' });
        this._domSpan = this._createContent(containerDiv, this._domElement);
    }

    render4Input(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        domElem.css({ overflow: 'hidden' });
        let domSpan = this._createContent(containerDiv, domElem);

        // //set value
        // domSpan.html(this._recordValue);
    }

    render4Printing(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        domElem.css({ overflow: 'hidden' });
        let domSpan = this._createContent(containerDiv, domElem);

        // //set value
        // domSpan.html(this._recordValue);
    }

    onUnSelect() {
        // console.log('label onUnSelect');
        super.onUnSelect();
    }

    _setDomStyles(domSpan, mainDomElement) {
        //undefined in case of editing
        if(domSpan == undefined)
            domSpan = this._domSpan;

        domSpan.css(this._styles);

        //special code for text-align
        if(this._styles['text-align'] != undefined && mainDomElement != undefined) {
            mainDomElement.css({ 'text-align': this._styles['text-align'] });
        }
    }
    
    _setDomStyleSingle(prop, val, domSpan) {
        if(domSpan == undefined)
            domSpan = this._domSpan;

        let obj = {};
        obj[prop] = val;
        if(prop == 'text-align') {
            if(this._domElement != null)
                domSpan = this._domElement;
            else {
                console.log('DEV - call to _setDomStyleSingle when not in editing mode???');
                return;
            }
        }
        domSpan.css(obj);
    }
    
    genElementInfo() {
        let elementInfo = super.genElementInfo();

        if(this._imageSource != null)
            elementInfo.imageSrc = this._imageSource;

        elementInfo.imageSizeOption = this._imageSizeOpt;

        return elementInfo;
    }

    setImageSizeOption(sizeOption) {
        this._imageSizeOpt = sizeOption;
        
        if(this._imageSizeOpt == Constants.IMAGE_SIZE_OPTION.STRETCH) {
            this._domSpan.addClass('form-image-stretch');
            this._domSpan.removeClass('form-image-fit');
            this._domSpan.removeClass('form-image-center');
        } 
        else if(this._imageSizeOpt == Constants.IMAGE_SIZE_OPTION.FIT) {
            this._domSpan.addClass('form-image-fit');
            this._domSpan.removeClass('form-image-stretch');
            this._domSpan.removeClass('form-image-center');
        }
        else if(this._imageSizeOpt == Constants.IMAGE_SIZE_OPTION.CENTER) {
            this._domSpan.addClass('form-image-center');
            this._domSpan.removeClass('form-image-fit');
            this._domSpan.removeClass('form-image-stretch');
        }
        else { //original
            this._domSpan.removeClass('form-image-stretch');
            this._domSpan.removeClass('form-image-fit');
            this._domSpan.removeClass('form-image-center');
        }
    }

    setImageSrcUrl(url) {
        this._imageSource = url;
        this._domSpan.attr('src', this._imageSource);
    }
}