import FormElement from "./form-element";
import Constants from "../constants";

export default class FormTextbox extends FormElement {
    constructor(elementInfo, parent, idx, parentPanel) {
        super(elementInfo, parent, idx, parentPanel);
	
        this._type = Constants.ELEMENT_TYPE.TEXTBOX;

        this._domLabel = null;
        this._domInput = null;

        this._observationType = {};

        //specific information
        if(elementInfo != null) {
            if(elementInfo.observationType != undefined)
                Object.assign(this._observationType, elementInfo.observationType);
        }
    }

    dispose() {
        if(this._domLabel != null)
            this._domLabel.remove();

        if(this._domInput != null)
            this._domInput.remove();

        this._observationType = undefined;

        super.dispose();
    }

    _createInput(containerDiv, mainDomElement) {
        //create span label
        let domInput = $('<input>').addClass('form-input');

        //apply styles
        this._setDomStyles(domInput);

        //add to main DomElement
        mainDomElement.append(domInput);

        return domInput;
    }

    _createLabel(containerDiv, mainDomElement) {
        //create span label
        this._domLabel = $('<div>').addClass('form-input-label');

        this._setLabelContent();

        // //show full label content (OT name) when hover on the label
        // this._domLabel.on('mouseenter', function(ev) {

        // });

        //add to main DomElement
        mainDomElement.append(this._domLabel);
    }

    _setLabelContent() {
        //show part of the OT name
        let span = $('<span>');
        this._domLabel.append(span);
        span.attr('title', this._observationType.name);
        if(this._observationType.name != undefined && this._observationType.name.length > 30) {
            let s = this._observationType.name.substr(0, 28) + '..';
            span.html(s);
        }
        else
            span.html(this._observationType.name);
    }

    render4Editing(containerDiv) {
        // console.log('label render4Editing');
        //create main DomElement
        this._domElement = this._createDomElement(containerDiv, true);

        //my specific content
        this._domInput = this._createInput(containerDiv, this._domElement);
        this._createLabel(containerDiv, this._domElement);
    }

    render4Input(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        this._createInput(containerDiv, domElem);
    }

    render4Printing(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        this._createInput(containerDiv, domElem);
    }

    onUnSelect() {
        // console.log('label onUnSelect');
        super.onUnSelect();
    }

    _setDomStyles(domInput) {
        //undefined in case of editing
        if(domInput == undefined)
            domInput = this._domInput;

        domInput.css(this._styles);
    }
    
    _setDomStyleSingle(prop, val, domSpan) {
        if(domSpan == undefined)
            domSpan = this._domInput;

        let obj = {};
        obj[prop] = val;
        domSpan.css(obj);
    }
    
    genElementInfo() {
        let elementInfo = super.genElementInfo();
        elementInfo.observationType = {};
        Object.assign(elementInfo.observationType, this._observationType);
        return elementInfo;
    }

    updateObservationType(dataObj) {
        console.log('updateObservationType', dataObj);
        Object.assign(this._observationType, dataObj);

        this._domLabel.html('');
        this._setLabelContent();
    }
}