/**
 * ResizeAnchor object for resizing the FormElement.
 * anchorType: one of the following
 *      7   0   1
 *      6       2
 *      5   4   3
 *
 * @module elements/ResizeAnchor
 */

 export default class ResizeAnchor {
    constructor(formElement, anchorType, position, container) {
        this._formElement = formElement;
        this._type = anchorType;
        this._container = container;

        //clone position instead of assign directly
        this._position = {
            x: position.x,
            y: position.y
        };

        this._dom = null;

        this._halfSize = 3;

        this._fixX = false;
        this._fixY = false;
        if(this._type % 4 == 0)
            this._fixX = true;
        else if(this._type % 2 == 0)
            this._fixY = true;

        this._dragStart = {
            x: -100,
            y: -100
        };

        this._createDom();
    }

    _createDom() {
        this._dom = $("<div>");
        this._dom.addClass("elem-anchor").addClass("elem-anchor-" + this._type);
        this._container.append(this._dom);
        this._dom.css({'left' : (this._position.x - this._halfSize) + 'px',
                        'top' : (this._position.y - this._halfSize) + 'px',
                        'width': (this._halfSize * 2 + 1) + 'px',
                        'height': (this._halfSize * 2 + 1) + 'px',
                        'display': 'none'
                    });
        
        this._registerEventsHandling();
    }

    _registerEventsHandling() {
        var _self = this;
        this._dom.on('mousedown', function(ev) {
            ev.stopPropagation();
        });
        this._dom.draggable({
            axis: (this._fixY == true)?"x":((this._fixX == true)?"y":false),
            drag: function(event, ui) {
                if(Math.abs(ui.position.left - _self._dragStart.x) + Math.abs(ui.position.top - _self._dragStart.y) < 2)
                    return;
                return _self.onMove({x: ui.position.left, y: ui.position.top});
            },
            start: function(event, ui) {
                _self._dom.addClass("anchor-dragging");
                _self._dragStart.x = ui.position.left;
                _self._dragStart.y = ui.position.top;
            },
            stop: function(event, ui) {
                _self._dom.removeClass("anchor-dragging");
                _self._formElement.onAnchorDrop(this);
            }
        });
    }

    dispose() {
        if(this._dom != null)
            this._dom.remove();
    }

    updatePosition(pos) {
        this._position.x = pos.x;
        this._position.y = pos.y;
        this._dom.css({'left' : (this._position.x - this._halfSize) + 'px',
                        'top' : (this._position.y - this._halfSize) + 'px'
                    });
    }

    onMove(position) {
        if(!this._fixX)
            this._position.x += position.x - this._dragStart.x;
        if(!this._fixY)
            this._position.y += position.y - this._dragStart.y;

        if(this._formElement.onAnchorDrag(this)) { //update successfully
            this._dragStart.x = position.x;
            this._dragStart.y = position.y;
            return true;
        }
        else { //not success (wrong position) -> rollback
            if(!this._fixX)
                this._position.x -= position.x - this._dragStart.x;
            if(!this._fixY)
                this._position.y -= position.y - this._dragStart.y;
            return false;
        }
    }

    show() {
        this._dom.css({display:'block'});
        this._dom.addClass('anchor-selecting');
    }

    hide() {
        this._dom.removeClass('anchor-selecting');
        this._dom.css({display:'none'});
    }
 }