import Constants from './constants';
import FormViewer from './formviewer';
import GlobalVars from './globalvars';

function getParams(name) {
	if (name = (new RegExp('[?&]' + encodeURIComponent(name) + '=([^&]*)')).exec(location.search))
		return decodeURIComponent(name[1]);
}

let sDomain = getParams("domain");
if(sDomain != undefined)
	GlobalVars.domain = sDomain;
console.log("API: " + GlobalVars.domain);

//original formId
let s = getParams("formId");
if(s != undefined)
    GlobalVars.formId = s;
console.log("formId: " + GlobalVars.formId);

//user info
s = getParams("userId");
if(s != undefined)
    GlobalVars.userId = s;
s = getParams("username");
if(s != undefined)
    GlobalVars.username = s;
console.log("user: " + GlobalVars.username + ', ' + GlobalVars.userId);

s = getParams("hasOT");
if(s != undefined)
    GlobalVars.hasOTs = (s === 'true') ? true : false;

///////////////////////////////////////////////////////////////////////////////

var mainForm = null;
var mainDiv = null;

export function initFormEditing(containerId) {
    console.log('initFormEditing');

    //get form info
    if(GlobalVars.formId !== Constants.DEFAULT_NEW_FORM_ID && parent.getFormData != undefined) {
        parent.getFormData(GlobalVars.formId, function(res) {
            console.log(res);
            showFormEditing(containerId, res);
        });
    }
    else {
        console.log('new empty form');
        showFormEditing(containerId, []);
    }

    //btns & inputs
    $("#txtFontSize").on('change', function(ev) {
        // console.log('onFontSize changed', ev);
        mscform.styleFontSize(ev.currentTarget.value);
    });
    $("#txtFontFamily").on('change', function(ev) {
        // console.log('onFontFamily changed', ev);
        mscform.styleFontFamily(ev.currentTarget.value);
    });
    $("#txtPaddingVert").on('change', function(ev) {
        mscform.stylePaddingVert(ev.currentTarget.value);
    });
    $("#txtPaddingHorz").on('change', function(ev) {
        mscform.stylePaddingHorz(ev.currentTarget.value);
    });
    $("#txtImageUrl").on('change', function(ev) {
        // console.log('onFontFamily changed', ev);
        mscform.onImageUrlChanged(ev.currentTarget.value);
    });
    $("#txtContainerHeight").on('change', function(ev) {
        // console.log('onFontFamily changed', ev);
        mscform.updateContainerHeight(ev.currentTarget.value);
    });
    $("#txtContainerPaddingBottom").on('change', function(ev) {
        // console.log('onFontFamily changed', ev);
        mscform.updateContainerPaddingBottom(ev.currentTarget.value);
    });

    console.log('initFormEditing end');
}

export function showFormEditing(containerId, lstElements) {
    if(mainForm != null)
        mainForm.dispose();

    mainDiv = $('#' + containerId);
    mainForm = new FormViewer(mainDiv, lstElements);
    mainForm.render4Editing(mainDiv);

    //list common infos & OTs
    if(parent.getListCommonInfos != undefined) {
        parent.getListCommonInfos(function(res) {
            fetchListCommonInfos('selectCommonInfos', res);
        });
    }
    
    if(GlobalVars.hasOTs && parent.getListOTs != undefined) {
        parent.getListOTs(function(res) {
            fetchListOTs('selectOTs', res);
        });
    }
                
    fetchImageOptions('selectImageOptions');
}

export function initFormOthers(containerId, lstElements, isPrintOrInput, commonInfoData) {
    console.log('initFormOthers, commonInfoData:', commonInfoData);
    if(mainForm != null)
        mainForm.dispose();

    mainDiv = $('#' + containerId);
    mainForm = new FormViewer(mainDiv, lstElements);

    //set commonInfoData values
    mainForm.setCommonInfoData(commonInfoData);

    if(isPrintOrInput) {
        mainForm.render4Printing(mainDiv);
        callOnPrintCompleted(mainDiv);
    }
    else
        mainForm.render4Input(mainDiv);
}

function callOnPrintCompleted(div) {
    let sCompletedFuncName = getParams('completedFunc');
    console.log('callOnPrintCompleted here', sCompletedFuncName);
    if(sCompletedFuncName != undefined && sCompletedFuncName != '') {
        console.log('call to parent ' + sCompletedFuncName);
        let sHtml = div.html();
        setTimeout(function() {
            var call = eval("parent." + sCompletedFuncName).call(null, sHtml);
        }, 200);
    }
}

export function showFormPrinting(containerId) {
    let div = $('#' + containerId);
    mainForm.render4Printing(div);
    callOnPrintCompleted(div);
}

export function showFormViewing(containerId) {
    let div = $('#' + containerId);
    mainForm.render4Input(div);
}

export function saveForm() {
    mainForm.saveForm();
}

export function printForm(containerId, paperWidth, paperHeight, paperMargin, pathToFormFolder) {
    console.log('printForm');
    let div = $('#' + containerId);
    mainForm.doPrinting(div, paperWidth, paperHeight, paperMargin, pathToFormFolder);
}

export function save2Pdf(containerId, filename, isBlob = false, blobCallbackFunction = undefined) {
    console.log('save2Pdf');
    // if(pathToFormFolder.indexOf('http') != 0)
    //     pathToFormFolder = document.location.origin + pathToFormFolder;
    // console.log('save2Pdf, pathToFormFolder: ' + pathToFormFolder);

    mainForm.save2Pdf(containerId, filename, isBlob, blobCallbackFunction);
}

export function registerPrintCallbacks(previewCallback, doPrintCallback, doSavePdfCallback) {
    let sRegisterFuncName = getParams('registerFunc');
    var call = eval("parent." + sRegisterFuncName).call(null, previewCallback, doPrintCallback, doSavePdfCallback);
}

