import FormElement from "./form-element";
import Constants from "../constants";

export default class FormLabel extends FormElement {
    constructor(elementInfo, parent, idx, parentPanel) {
        super(elementInfo, parent, idx, parentPanel);
	
        this._type = Constants.ELEMENT_TYPE.LABEL;

        this._domSpan = null;
        this._editingTextbox = null;
        this._isEditingLabel = false;

        //specific information
        if(elementInfo != null) {
            this._labelContent = this._value; //elementInfo.value; (same)
        }
        else {
            this._labelContent = "";
        }
    }

    dispose() {
        if(this._domSpan != null)
            this._domSpan.remove();

        if(this._editingTextbox != null)
            this._editingTextbox.remove();

        super.dispose();
    }

    _renderCommon(containerDiv, mainDomElement) {
        //create span label
        let domSpan = $('<span>').addClass('form-label');

        //apply styles
        this._setDomStyles(domSpan);

        //set value
        domSpan.html(this._labelContent);

        //add to main DomElement
        mainDomElement.append(domSpan);

        return domSpan;
    }

    render4Editing(containerDiv) {
        console.log('label render4Editing');
        //create main DomElement
        this._domElement = this._createDomElement(containerDiv, true);

        //my specific content
        this._domSpan = this._renderCommon(containerDiv, this._domElement);

        //controlling
        var _self = this;
        this._ctrlElement.dblclick(function() {
            // console.log('dblclick here, _isSelected: ' + _self._isSelected);
            if(_self._isEditingLabel)
                return;

            if(!_self._isSelected)
                _self.onSelect(null);

            _self.showLabelEditing();
        });
    }

    render4Input(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        this._renderCommon(containerDiv, domElem);
    }

    render4Printing(containerDiv) {
        //create main DomElement
        let domElem = this._createDomElement(containerDiv, false);

        //my specific content
        this._renderCommon(containerDiv, domElem);
    }

    showLabelEditing() {
        this._isEditingLabel = true;
        this._parentForm.disableKeyProcessing();
        if(this._editingTextbox == null) {
            this._editingTextbox = $('<input>').attr('type', 'text').css({width:'99%', height: '99%'});
            this._ctrlElement.append(this._editingTextbox);

            //TODO: enter event
            var _self = this;
            this._editingTextbox.keyup(function(ev) {
                if(ev.which == 13) {
                    ev.preventDefault();

                    _self._updateLabelContent(_self._editingTextbox.val());

                    _self._isEditingLabel = false;
                    _self._editingTextbox.css({display: 'none'});
                    _self._parentForm.enableKeyProcessing();
                }
            });
        }
        else {
            this._editingTextbox.css({display: 'block'});
        }
        this._editingTextbox.val(this._labelContent);
    }

    onUnSelect() {
        // console.log('label onUnSelect');
        super.onUnSelect();
        if(this._editingTextbox != null && this._isEditingLabel) {
            this._isEditingLabel = false;
            this._editingTextbox.css({display: 'none'});
            this._parentForm.enableKeyProcessing();

            this._updateLabelContent(this._editingTextbox.val());
        }   
    }

    _updateLabelContent(content) {
        this._labelContent = content;
        this._value = content;

        this._domSpan.html(this._labelContent);

        //update height bounding base on content
        let height = this._domSpan.height();
        if(height > this._bounding.height) {
            this._bounding.height = height;
            this._domElement.css({ height: height + 'px' });
            this._ctrlElement.css({ height: height + 'px' });
            this._updateAnchors(false);
        }
    }

    _setDomStyles(domSpan) {
        //undefined in case of editing
        if(domSpan == undefined)
            domSpan = this._domSpan;

        domSpan.css(this._styles);
    }

    _setDomStyleSingle(prop, val, domSpan) {
        if(domSpan == undefined)
            domSpan = this._domSpan;

        let obj = {};
        obj[prop] = val;
        domSpan.css(obj);
    }

}